.divPreloader{
    position: relative;
}

.loading {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.8;
    background-color: #fff;
    z-index: 99
  }
  

  .loading_image{
    z-index: 100;
  }


  .fitimage{
    height: 100%; width: 10%; object-fit: contain
  }